@keyframes modal-show {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}


@keyframes shake {
  0%,
  100% {
    transform: translateX(-5px);
  }
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(5px);
  }
  20%,
  40%,
  60%,
  80% {
    transform: translateX(-5px);
  }
}
